import GraveStoneIcon from "./GraveStone.png"
import ChestIcon from "./Chest.png"
import BoneIcon from "./8Bone.png"
import PresentIcon from "./Present.png"
import MerchIcon from "./Merch.png"
import "./features.css"

function Features() {
  return (
    <div className="features-cnt">

            {/* Features */}

          <div id="#features" class="features nes-container is-rounded is-centered with-title">
          <span class="title">Features</span>

        <div class="features-cnt">
          <div class="feature-cnt">
          <img class="bone-icn" src={BoneIcon}></img>
          <h1> HARVEST $8BONE</h1>
          <div class="nes-container is-rounded">
            <p>Use your BitBones to harvest <span>$8BONE</span></p>
            <p>If a holder stakes 1 BitBone, they will receive $8BONE</p>
            <p>This token will be used for utility such as renaming your BitBone or paying for 8BONE Merch.</p>
            </div>

          <div class="feature-cnt">  
          </div>
          <img class="chest-icn" src={ChestIcon}></img>
          <h1>WEEKLY AIRDROPS</h1>
          <div class="nes-container is-rounded">
            <p>Members of the BoneYard automatically get added to the weekly airdrop list</p>
          </div>
          </div>
          </div>

        <div class="features-cnt">
          <div class="feature-cnt">
          <img class="present-icn" src={PresentIcon}></img>
          <h1>EXCLUSIVE GIVEAWAYS</h1>
          <div class="nes-container is-rounded">
            <p>BoneHeads will be granted access to some of our biggest giveaways that non-BoneHeads don't have access to!</p>
          </div>
          </div>
          
          <div class="feature-cnt">
          <img class="graveyard-icn" src={GraveStoneIcon}></img>
          <h1>GRAVEYARD DAO</h1>
          <div id="graveyard"class="nes-container is-rounded">
            <p>A community of Boneheads that love to share their BitBones and opportunities they have by owning one. This community culminates into a decision-making organization for the advancement of BitBones as a whole!</p>
          </div>
          </div>
          </div>

          <div class="features-cnt">
          <div class="feature-cnt">
          <img class="merch-icn" src={MerchIcon}></img>
          <h1>MERCHANDISE</h1>
          <div class="nes-container is-rounded">
            <p>BoneHeads will have some of the drippiest merch out there.  We are looking to collaborate with some talented designers on some luxury quality material!</p>
          </div>
          </div>
        </div>
          
    </div>
    </div>
  );
}

export default Features;