import BonePreview from './sneak-peak-bone.gif';
import React from 'react';
import ReactDOM from 'react-dom';
import Countdown from 'react-countdown';
import "./countdown.css"


const mintPrice = 40;
const tokenPayment = "MATIC";
const mintedSupply = "0";
const totalSupply = "2000";
function Mint() {


    const renderer = ({ days, hours, minutes, seconds, completed }) => {
    if (completed) {
      // Render a completed state
      return <span> <br/> <p class="launch-cd">BitBones are Alive!</p><div style={{marginBottom: "2em"}}><button 
      class="mint-btn nes-btn is-warning">Go to Mint</button></div></span>;
    } else {
      // Render a countdown
      return <span><h1>COMING SOON</h1>{days}d {hours}h {minutes}m {seconds}s <br/> <p class="launch-cd">Until Launch</p></span>;
        }
    }; 
  return (
      
    <div className="mint-cmp">
           {/* Mint */}
           <div class="mint is-rounded nes-container is-centered with-title">
            <span class="title">Mint a Bone</span>
           
           
           <Countdown
    date="2021-11-02T08:00:00"
    intervalDelay={0}
    precision={0}
    autoStart="true"
    renderer={renderer}
  />
  
     
    
    <img class="bone-md" src={BonePreview}></img>
    {/*}<p>1 BONE = {mintPrice} MATIC</p>
    <p>Total Minted:</p>*/}
    <div class="mint-count">
    <a><span>NOV 7 @ 4PM UTC</span></a>
  </div>
     
     


       <div class="mint-details is-rounded is-centered nes-container with-title">
          <span class="title">Details</span>
          <ul class="mint-price nes-list is-disc">
            <li>Mint Price: {mintPrice} {tokenPayment}</li>
            <li>Total Bones: {totalSupply}</li>
            <a href="/#home" ><li>Verified Contract Address</li></a>
          </ul>
            </div>
            </div>
        </div>
    
  );
}

export default Mint;